  --black: #202025;
  --gray: #5f6775;
  --modalgray: #5f6775;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.themeDark {
  --white: #202025;
  --black: #fcfcfc;
  --gray: #fcfcfc;
  --modalgray: #bdbdbd;
}

